import React from 'react';
import HeaderContainer from "../../container/header/HeaderContainerOne";
import Logo from "./elements/logo";
import Navbar from "./elements/navbar/Navbar";
import HeaderConfig from "./elements/config/HeaderConfigOne";

import logo from '../../assets/img/logo/logo-light.png'

const HeaderThree = () => {
    return (
        <HeaderContainer
            logoVersion={'light'}
            transparent={true}
            sticky={false}
        >
            <div className="header-left flex-20">

            </div>
            <div className="header-flex-right">
                <Navbar/>
                <div className="header-left flex-20">
                    <Logo logo={logo}/>
                </div>
                <HeaderConfig/>
            </div>
        </HeaderContainer>
    );
};

export default HeaderThree;