import React from 'react';
import {Container, Col} from "react-bootstrap";

const SliderAuthenticStudio = () => {

    return (
        <div className="hero-slider hero-slider--digital-agency">
            <div className="swiper-slide">
                <div
                    className="slide-inner slide-bg-image"
                    style={{backgroundImage: `url(${require('../../assets/img/revolution/slider-wallpaper-1.png')})`}}
                >
                    <Container fluid>
                        <Col lg={12} className="text-center">
                            <div className="slide-content">
                                <div style={{width: "100%",marginTop: "70vh",marginLeft:"0%"}} data-swiper-parallax="300" className="slide-title">
                                    <h2 className="title"  style={{textAlign: "right",fontSize:"59px"}}>Kzm Ferik</h2>
                                    <h2 className="subtitle" style={{textAlign:"right",fontSize: "55px"}}>Ihr Berater fr Ihren finanziellen Erfolg !</h2>
                                </div>
                            </div>
                        </Col>
                    </Container>
                </div>
            </div>
        </div>
    );
};


export default SliderAuthenticStudio;
